import React, { useState } from "react";
import { colors } from "../../utils/enum";

const InputText = ({
  _ref,
  p,
  theme,
  maxLength,
  borderRadius,
  placeholder,
  error,
  autoFocus = true,
  disabled = false,
  value,
  border,
  onClick,
  height,
  width
}) => {
  const [val, setVal] = useState(value || '');
  const onChange = (event) => {
    setVal(event?.target?.value);
  };

  return (
    <>
      <input
        onChange={onChange}
        onClick={onClick}
        autoComplete="off"
        autoFocus={autoFocus}
        name="custom-input-text"
        ref={_ref}
        id="new-item-input"
        placeholder={placeholder}
        maxLength={maxLength}
        disabled={disabled}
        value={val}
        style={{
          borderRadius: borderRadius || 0,
          padding: p || "1rem",
          fontFamily: "Urbanist",
          fontSize: "1rem",
          color: colors[theme]?.generalColors.fontColor,
          border: border || `1px solid ${error ? "#fc95a1" : colors[theme]?.generalColors.lightBorder}`,
          background: "none",
          outline: "none",
          width: width || 'auto',
          height: height || 'auto',
        }}
      />
    </>
  );
};

export default InputText;
