import { Typography, Stack, IconButton } from "@mui/material";
import LogoutIcon from "@mui/icons-material/Logout";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import React from "react";
import { colors, mobileWidth, themes } from "../../utils/enum";
import { useNavigate } from "react-router-dom";
import { truncateString } from "../../utils/helper";
import "./UserTopMenu.scss";

const UserTopMenu = ({ user, bgColor, theme, handleLogout, disabled, color, backButton = true }) => {
  const navigate = useNavigate();
  return (
    <Stack
      direction={"row"}
      padding={1}
      mt={2}
      spacing={2}
      justifyContent={"space-between"}
      alignItems={"center"}
      borderRadius={5}
      bgcolor={bgColor}
      width={"90vw"}
      maxWidth={mobileWidth}
      color={colors[theme]?.generalColors.fontColor}
    >
      <Typography className="typography" variant="h5" color={color} fontFamily={"Urbanist"}>
        {/*  Back Button */}
        {backButton && (
          <IconButton size={"small"} disableRipple onClick={() => navigate(-1)}>
            <ArrowBackIosIcon sx={{ color: color }} />
          </IconButton>
        )}

        {/* Avatar */}
        <div
          className={"avatar"}
          onClick={() => navigate("/profile")}
          style={{ background: theme === themes.lightTheme ? "lightgray" : "#707070" }}
        >
          {user?.name[0] || "U"}
        </div>
        <span style={{ width: "10px" }} />
        {user?.name && truncateString(user?.name)}
      </Typography>

      {/* Log out button */}
      <IconButton disabled={disabled} onClick={handleLogout}>
        <LogoutIcon fontSize={"small"} sx={{ color }} />
      </IconButton>
    </Stack>
  );
};

export default UserTopMenu;
